  /* Modal Styles */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: -10vw;
  width: 100%;
  height: 120%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #444444;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #ff1919;
  width: 80%;
  color: wheat;
  text-align: center;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #ef0808;
  text-decoration: none;
  cursor: pointer;
}
