#A1{
    width: 60px;
    height: auto;
    background: #1a1a1a;
    cursor: pointer;
    border: 2px solid #ff6b6b;
    box-shadow: 0px 0px 20px #ff6b6b;
    border-radius: 5px;
    transition: background-color 0.2s ease-in-out;
}

